.header {
	position: sticky;
	top: -6.5rem;
	left: 0;
	z-index: 31;
	box-sizing: border-box;
	width: 100%;
	height: 10rem;
	background-color: #fff;
}

.header-top {
	box-sizing: border-box;
	width: 100%;
	height: 6.5rem;
	background-color: #fff;
	transition: all .5s ease .01ms;
	font-size: 1.7rem;
}

.header-top h1 {
	text-align: center;
}

.header-top h1 a {
	display: inline-block;
}

.header-top h1 img {
	height: 37px;
	transition: height .5s ease 0.01ms;
}

.header-top .btn-drawer {
	display: inline-block;
	position: absolute;
	top: 26px;
	right: 30px;
	color: #000;
	font-size: 14px;
	font-weight: 400;
	transition: top 0.5s ease 0.01ms;
}

.header-top .btn-drawer-text {
	vertical-align: middle;
}

.header-top .btn-drawer-closetext {
	display: none;
}

.header-top .btn-drawer .icon {
	position: relative;
	display: inline-block;
	width: 21px;
	height: 17px;
}

.header-top .btn-drawer .line {
	position: absolute;
	top: 50%;
	left: 0;
	display: block;
	width: 17px;
	height: 3px;
	margin-top: -1.5px;
	background-color: #323232;
	transform-origin: center;
	transition: transform 0.3s ease-in-out, margin-top 0.15s ease-in-out 0.3s, opacity 0.15s ease-in-out 0.3s, background 0.3s ease;
}

.header-top .btn-drawer .line-1 {
	transform: translateY(-6px);
}

.header-top .btn-drawer .line-2 {
	transition-delay: 0.2s;
}

.header-top .btn-drawer .line-3 {
	transform: translateY(6px);
}

.header-top .btn-drawer.on .line {
	transition: transform 0.3s ease-in-out, margin-top 0.15s ease-in-out, opacity 0.15s ease-in-out 0.1s;
}

.header-top .btn-drawer.on .line-1 {
	transform: rotate(135deg);
}

.header-top .btn-drawer.on .line-2 {
	opacity: 0;
}

.header-top .btn-drawer.on .line-3 {
	transform: rotate(225deg);
}

.header-top .btn-drawer.on-opentext {
	display: none;
}

.header-top .btn-drawer.on-closetext {
	display: inline-block;
}

.header-nav {
	scrollbar-width: none;
	-ms-overflow-style: none;
	font-size: 1.5rem;
	overflow-x: visible;
	overflow-y: hidden;
	white-space: nowrap;
	-webkit-overflow-scrolling: touch;
	padding-left: 3.1rem;
	color: #000;
	letter-spacing: 0;
}

.header-nav::-webkit-scrollbar {
	display: none;
}

.header-nav a {
	position: relative;
	display: inline-block;
	padding: 1rem 1.2rem 1rem 1.3rem;
}

.header-nav a.active:after {
	display: block;
	content: '';
	position: absolute;
	top: 0.2rem;
	left: 50%;
	z-index: 1;
	width: .5rem;
	height: .5rem;
	margin-left: -.2rem;
	border-radius: 50%;
	background-color: #000;
}

.footer {
	padding-top: 65px;
	background-color: #f2f2f2;
}

.footer .inner {
	max-width: 1300px;
	margin: 0 auto;
}

.footer .link-list {
	position: relative;
	display: inline-block;
}

.footer .link-list .selectbox {
	font-size: 13px;
	display: inline-block;
	width: 215px;
	vertical-align: middle;
	overflow: visible;
	border: 0;
}

.footer .link-list .label {
	text-align: left;
	box-sizing: border-box;
	display: block;
	width: 100%;
	height: 100%;
	padding-right: 40px;
	padding-left: 20px;
	border: 1px solid #ccc;
}

.footer .link-list ul {
	display: none;
}

.footer .link-list.active ul {
	display: block;
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 10;
	box-sizing: border-box;
	width: 100%;
	margin-top: -1px;
	border: 1px solid #ccc;
	background-color: #fff;
}

.footer .link-list.active ul a {
	display: block;
	height: 100%;
	padding-right: 40px;
	padding-left: 20px;
}

.footer .link-list.active ul a:hover {
	background-color: #f4f4f4;
}

.footer-menu-list {
	display: table;
	width: 100%;
	max-width: 1300px;
	margin: 0 auto 65px;
}

.footer-menu-list:after {
	content: '';
	display: block;
	clear: both;
}

.footer-menu-list .menu-item {
	display: table-cell;
	width: 25%;
	padding-left: 20px;
	box-sizing: border-box;
}

.footer-menu-list .menu-item strong {
	display: block;
	margin-bottom: 15px;
	font-size: 18px;
	font-weight: 700;
	line-height: 1.5;
}

.footer-menu-list .menu-item li a {
	color: #0061a0;
	font-size: 15px;
	line-height: 1.8;
}

.footer-site {
	position: relative;
	margin-bottom: 40px;
}

.footer-site .sns {
	position: absolute;
	bottom: 0;
	right: 50px;
}

.footer-site .sns a {
	display: inline-block;
	margin-left: 15px;
	font: 0/0 a;
}

.footer-info {
	padding-bottom: 75px;
	border-top: 1px solid #ccc;
}

.footer-info:after {
	content: '';
	display: block;
	clear: both;
}

.footer-logo {
	float: left;
	margin-top: 75px;
	margin-right: 68px;
}

.footer-etc {
	float: left;
	margin-top: 40px;
	font-size: 14px;
	line-height: 1.7;
}

.footer-etc .link-list .label {
	display: none;
}

.footer-etc .link-list ul {
	display: block;
}

.footer-etc .link-policy {
	margin-bottom: 13px;
}

.footer-etc .link-policy:after {
	content: '';
	display: block;
	clear: both;
}

.footer-etc .link-policy li {
	float: left;
	margin-right: 25px;
}

.footer-etc .link-policy li a {
	color: #0061a0;
	line-height: 2.1;
}

.footer-award {
	position: relative;
	float: right;
	margin-top: 67px;
}

.footer-award .icon-award {
	position: absolute;
	top: 50%;
	left: 0;
	margin-top: -26px;
}

.footer-award span {
	display: inline-block;
	padding-left: 49px;
	font-size: 12px;
	line-height: 1.5;
	color: #666;
}

.fixedBottom {
	display: flex;
	justify-content: space-between;
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 41;
	width: 100%;
	height: 50px;
	padding: 0 13px;
	font: 0/0 a;
	background-color: #222;
	box-sizing: border-box;
	transform: translate3d(0, 0, 0);
	transition: transform .2s;
}

.fixedBottom button {
	color: #fff;
	width: auto;
	font-size: 1rem;
}

.fixedBottom.close {
	transform: translate3d(0, 100%, 0);
}

.drawer {
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	z-index: 70;
	background-color: #fff;
	transform: translateX(100%);
	transition: transform .4s;
	background-repeat: no-repeat;
	background-image: url("../../resources-mo/images/icons/error.png");
	background-position: 0, 0;
	background-size: auto;
}

.drawer.on {
	transform: translateX(0);
}
